body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(https://wonderfulengineering.com/wp-content/uploads/2014/06/galaxy-wallpapers-35.jpg) no-repeat center center fixed black;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


input, select {
	outline: none;
}
    
    input::-webkit-input-placeholder {
      color: #9E9E9E;
      opacity: 1;
    }
    
    input::-ms-input-placeholder {
      color: #9E9E9E;
      opacity: 1;
    }
    
    input::placeholder {
      color: #9E9E9E;
      opacity: 1;
    }
    input:focus::-ms-input-placeholder {
      color: #9E9E9E;
    }
    input:focus:-ms-input-placeholder {
      color: $color--additional2;
    }
    input:focus,
    select:focus,
    input:not(:placeholder-shown) {
      color: #000000;
      border-color: #2196F3 ;
    }
  
nav {
  width: 100%;
  margin: 0;
  top: 0;
  background: #212121;
  color: #ccc;
  z-index: 6;
  padding: 40px ; /* Large padding which will shrink on scroll (using JS) */
  transition: 0.4s; /* Adds a transition effect when the padding is decreased */
  position: fixed;
  border-bottom: yellow 50px;
}

.fixed-nav {
  position: fixed;
}



